import { handleActions } from 'redux-actions'
import { uniqBy } from 'lodash'
import { UiState } from '../../types'
import {
  LOAD_ISSUE_SUCCESS,
  loadIssueSuccessActionType,
  LOAD_ISSUES_SUCCESS,
  loadIssuesSuccessActionType,
  ADD_ISSUE_SUCCESS,
  addIssueSuccessActionType,
  DELETE_ISSUE_SUCCESS,
  deleteIssueSuccessActionType,
  EDIT_ISSUE_SUCCESS,
  editIssueSuccessActionType,
  LOAD_NOTIFICATIONS_SUCCESS,
  loadNotificationsActionType,
  LOAD_NOTIFICATION_SUCCESS,
  loadNotificationSuccessType,
  SET_OFFSET,
  setOffsetActionType,
  setTopLoaderActionType,
  setMainLoaderActionType,
  SET_TOP_LOADER,
  SET_MAIN_LOADER,
  RESET_ISSUE_SUCCESS_CREATED,
  uploadFileSuccessActionType,
  UPLOAD_FILE_SUCCESS,
  uploadFilesSuccessActionType,
  UPLOAD_FILES_SUCCESS,
  REMOVE_FILE_SUCCESS,
  removeFileSuccessActionType,
} from '../actions/ui'
import dayjs from 'dayjs'

const initialState: UiState = {
  attachments: [],
  issues: [],
  issueSuccessCreated: false,
  lastIssueNumber: 0,
  notifications: [],
  offset: 0,
  topLoader: false,
  mainLoader: false,
}

const reducers = {
  [LOAD_ISSUE_SUCCESS]: (state: UiState, { payload }: loadIssueSuccessActionType): UiState => {
    return { ...state, issues: [...state.issues.filter(({ id }) => id !== payload.id), payload] }
  },
  [LOAD_ISSUES_SUCCESS]: (state: UiState, { payload }: loadIssuesSuccessActionType): UiState => {
    return { ...state, issues: uniqBy([...state.issues, ...payload], 'id'), attachments: [] }
  },
  [ADD_ISSUE_SUCCESS]: (state: UiState, { payload: data }: addIssueSuccessActionType): UiState => {
    return {
      ...state,
      issueSuccessCreated: true,
      lastIssueNumber: data.id,
      issues: [data, ...state.issues],
      attachments: [],
    }
  },
  [RESET_ISSUE_SUCCESS_CREATED]: (state: UiState) => ({ ...state, issueSuccessCreated: false }),
  [DELETE_ISSUE_SUCCESS]: (state: UiState, { payload }: deleteIssueSuccessActionType): UiState => {
    return { ...state, issues: [...state.issues.filter(issue => issue.id !== payload)] }
  },
  [EDIT_ISSUE_SUCCESS]: (state: UiState, { payload }: editIssueSuccessActionType): UiState => {
    return {
      ...state,
      issues: state.issues.map(issue => {
        if (issue.id !== payload.id) return issue
        return payload
      }),
    }
  },
  [LOAD_NOTIFICATIONS_SUCCESS]: (state: UiState, { payload }: loadNotificationsActionType): UiState => {
    return { ...state, notifications: payload }
  },
  [LOAD_NOTIFICATION_SUCCESS]: (state: UiState, { payload }: loadNotificationSuccessType): UiState => {
    return {
      ...state,
      notifications: [
        {
          ...payload,
          timestamp: dayjs.unix(+payload.timestamp).format('DD.MM.YYYY, HH:mm'),
        },
        ...state.notifications,
      ],
      issues: state.issues.map(issue => {
        if (issue.id !== payload.issueId) return issue

        return {
          ...issue,
          status: payload.statusNext,
        }
      }),
    }
  },
  [SET_OFFSET]: (state: UiState, { payload }: setOffsetActionType): UiState => {
    return { ...state, offset: payload }
  },
  [SET_TOP_LOADER]: (state: UiState, { payload: topLoader }: setTopLoaderActionType): UiState => {
    return {
      ...state,
      topLoader,
    }
  },
  [SET_MAIN_LOADER]: (state: UiState, { payload: mainLoader }: setMainLoaderActionType): UiState => {
    return {
      ...state,
      mainLoader,
    }
  },
  [REMOVE_FILE_SUCCESS]: (state: UiState, { payload: id }: removeFileSuccessActionType): UiState => {
    if (!id) return state

    return {
      ...state,
      attachments: state.attachments.filter(attachment => attachment.fileId !== id),
    }
  },
  [UPLOAD_FILE_SUCCESS]: (state: UiState, { payload: file }: uploadFileSuccessActionType): UiState => {
    return {
      ...state,
      attachments: [...state.attachments, file],
    }
  },
  [UPLOAD_FILES_SUCCESS]: (state: UiState, { payload: files }: uploadFilesSuccessActionType): UiState => {
    return {
      ...state,
      attachments: [...state.attachments, ...files],
    }
  },
}

export const ui = handleActions<UiState, any>(reducers, initialState)
